.business_main {
  width: 100%;
}
.card {
  /* z-index: 9999; */
  width: 30%;
  border-radius: 20px;
}
#user {
  border-radius: 30px;
}
.B-plan-h1 {
  color: #000;
  text-align: center;

  font-size: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.B-plan-p {
  color: #181818;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 26px;
}
.B-plan_bottom {
  position: absolute;
  bottom: 0px;
  z-index: -1;
}


.text {
  color: #d13a49;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 28px; /* 175% */
  cursor: pointer;
}


.cardDisplay {
  display: flex;
  justify-content: space-around !important;
}
#B-plan-key {
  text-decoration: none;
  color: hsl(354, 62%, 52%);
  margin: 20px 0px 20px 0px;
  padding: 10px;
  float: left;
}
#key {
  float: left;
  margin: 35px 0px 35px 0px;
}
.btn-close {
  position: absolute;
  opacity: 10;
  margin: 5px;
  right: 10px;
}
#B-plan-label {
  text-align: start;
  margin: 8px;
  margin-left: 15px;
  font-size: 25px;
}
#B-plan-field {
  padding: 15px;
  width: 100%;
  position: relative;
  margin-bottom: 30px;
}
#icon {
  background-color: #d13a49;
  position: absolute;
  right: 16px;
  border: none;
  padding: 14px 25px 14px 25px;
}
#img-icon {
  width: 30px;
}
#icon-2 {
  background-color: #d13a49;
  position: absolute;
  right: 16px;
  border: none;
  padding: 17px 25px 17px 25px;
  color: white;
}
#modal {
  width: 200%;
}
#B-plan-label-2 {
  font-size: 21px;
  text-align: start;
  margin: 8px;
}

/* new  */
.custom_card {
  border-radius: 20px;
  background: #fff;
  box-shadow: 0px 0px 15px 7px rgba(0, 0, 0, 0.05);
  width: 30%;
}
.custom_card .business_img {
  width: 97%;
  height: auto;
  border-radius: 20px;
  margin: 0 auto;
}

.business_list{
  list-style-image: url("../../Assets/arrow.svg");
}
.business_list li{
  line-height: 30px;
  color: #181818;
  font-family: Quicksand;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 26px; 
  padding: 10px 0;
}