/* =====================Hover CSS======================= */

.tool {
  position: relative;
  display: block;
  color: black;
  cursor: pointer;
}

.tool .tooltiptext {
  visibility: hidden;
  width: 300%;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  background-color: white;
  color: black;
  text-align: center;
  border-radius: 10px;
  padding: 16px;
  position: absolute;
  z-index: 1;
  font-size: 16px;
  bottom: -250%;
  font-size: 15px;
  left: 140%;
  margin-left: -60px;
  color: #000;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
}

.tool .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 150%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

.tool:hover .tooltiptext {
  visibility: visible;
}
