#form-head {
  margin: 20px 0;
  padding: 40px 0;
}

#form-h1 {
  font-family: "Noto Sans", sans-serif;
  font-family: "Quicksand", sans-serif;
  color: #000;
  text-align: center;

  font-size: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
#form-p {
  font-family: "Noto Sans", sans-serif;
  font-family: "Quicksand", sans-serif;
  color: #181818;
  text-align: center;

  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 26px; /* 162.5% */
}
#form-h5 {
  font-family: "Noto Sans", sans-serif;
  font-family: "Quicksand", sans-serif;
  color: #141414;
 padding-top: 60px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 30px; /* 150% */
}
#form-box-p {
  color: #828282;
  font-family: "Noto Sans", sans-serif;
  font-family: "Quicksand", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px; /* 187.5% */
}
#icons {
  width: 44px;
  height: 44px;
  flex-shrink: 0;
  
}
#form-inputs {
  padding: 5px;
  margin: 15px;
  margin-top: 0px;
  border-color: 1px solid;
}
#form-textarea {
  width: 95%;
  margin: 0px 15px 0px 15px;
  height: 67%;
  border-color: 1px solid;
}

.contact_bottom {
  position: absolute;
  bottom: 0px;
  z-index: -1;
}
/* .contact_main {
  position: relative;
  height: 100vh;
} */

.form-middle {
  z-index: 9999;
}
.inputs::placeholder{
 text-indent: 10px;
 color: #828282;
font-family: Quicksand;
font-size: 16px;
font-style: normal;
font-weight: 500;
line-height: 26px; 
}
.contact_main{
  background: url("../Assets/Vector\ 2.svg");
  width:100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 40px 0 80px;

}