button:focus-visible {
  outline: none !important;
  box-shadow: none !important;
}
.primary_clr {
  color: #d13a49;
}

.custom_btn {
  color: #fff;
  text-align: center;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  width: 100%;
  height: 50px;
  flex-shrink: 0;
  border-radius: 5px;
  background: #d13a49;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: inherit;
  transition-property: all;
  transition-duration: .6s;
  transition-timing-function: ease;
}
.custom_btn a {
  text-decoration: none;
  color: white;
}
.custom_btn:hover {
  background-color: #fff;
  color: #d13a49 !important;
  border: 1px solid #d13a49;

  
}
.custom_btn:hover a{
  color: #d13a49 !important;
}

.btn_link {
  color: #d13a49;
  font-family: Quicksand;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 28px;
  white-space: nowrap;
}
